$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-col-padding-top: 0;
  --mt-col-padding-bottom: 0;
  --mt-col-max-width: 100%;
  --mt-col-background-color: null;

  background-color: var(--mt-col-background-color);
  width: 100%;
  display: block;
  flex-shrink: 1;
  box-sizing: border-box;
  flex-shrink: 0;
  max-width: var(--mt-col-max-width);
  padding-top: var(--mt-col-padding-top);
  padding-right: calc(var(--mt-gutter-right) * 0.5);
  padding-bottom: var(--mt-col-padding-bottom), var(--mt-col-padding-bottom);
  padding-left: calc(var(--mt-gutter-left) * 0.5);
}

@each $mt-breakpoint-key, $mt-breakpoint-value in $mt-breakpoints {
  @media (min-width: $mt-breakpoint-value) {
    .mt-col.mt-#{$mt-breakpoint-key} {
      flex: 1 0 0;
      max-width: var(--mt-col-#{$mt-breakpoint-key}-max-width, var(--mt-col-max-width));
      padding-top: var(
        --mt-col-#{$mt-breakpoint-key}-padding-top,
        var(--mt-col-padding-top)
      );
      padding-right: calc(
        var(--mt-gutter-#{$mt-breakpoint-key}-right, var(--mt-gutter-right)) * 0.5
      );
      padding-bottom: var(
        --mt-col-#{$mt-breakpoint-key}-padding-bottom,
        var(--mt-col-padding-bottom)
      );
      padding-left: calc(
        var(--mt-gutter-#{$mt-breakpoint-key}-left, var(--mt-gutter-left)) * 0.5
      );
    }
  }
}
