import { Component, h, Host, Prop } from '@stencil/core';
import { generateClassesLayout } from '../../global/typescript/functions';

@Component({
  tag: 'mt-col',
  styleUrl: 'col.scss',
  scoped: true,
})
export class Col {
  @Prop({ reflect: true }) breakpoint: string;

  render() {
    const { breakpoint } = this;
    return (
      <Host
        class={generateClassesLayout(breakpoint, {
          'mt-col': true,
        })}
      >
        <slot></slot>
      </Host>
    );
  }
}
