import type { MtCssClassMap } from './types';

export const generateClasses = (
  produto: string,
  apostila: string,
  ano: string,
  especialidade?: string,
  parte?: string,
  cssClassMap?: MtCssClassMap,
): MtCssClassMap => {
  return {
    [`mt-${produto}`]: true,
    [`mt-${apostila}`]: true,
    [`mt-${ano}`]: true,
    [`mt-${especialidade}`]: !!especialidade,
    [`mt-${parte}`]: !!parte,
    ...cssClassMap,
  };
};

export const generateClassesLayout = (
  breakpoint: string,
  cssClassMap?: MtCssClassMap,
): MtCssClassMap => {
  return {
    [`mt-${breakpoint}`]: true,
    ...cssClassMap,
  };
};
